import BackTop from "../components/backTop/backTop.vue";

export const backTop = {
    components: {
        BackTop,
    },
    data(){
        return {
            isShow: false,
        }
    },
    methods:{
        backTop() {
            this.$refs.scroll.scrollTo(0, 0, 500);
        },
        listenBackScroll(position){
            this.isShow = position.y < -1000;
        }
    }
}
